      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" outlined @click="setOrderDelete" v-show="deleteBtnShow"> Delete </v-btn>
        <v-btn color="success" @click="setOrderModify" v-show="modifyBtnShow"> Modify </v-btn>
        <v-btn color="primary" @click="setOrderOk"> Ok </v-btn>
      </v-card-actions>
