<script>
import common from "@/common";

export default {
  props: {
    setOrderDetail: {
      type: Object
    }
  },
  data () {
    return {
      currencyItems: common.currencyItems,
      shippingTermItems: common.shippingTermItems,
      serviceTypeItems: common.serviceTypeItems,
      packageTypeItems: common.packageTypeItems,
      contentsTypeItems: common.contentsTypeItems,
      orderDetail: "",
    }
  },
  updated() {
      this.orderDetail = this.setOrderDetail.orderDetail;
      document.getElementById('dialog').scrollIntoView({ behavior: 'auto' })
  },
  computed: {
    show () {
      return this.setOrderDetail.show
    },
    deleteBtnShow () {
      return this.setOrderDetail.deleteBtnShow
    },
    modifyBtnShow () {
      return this.setOrderDetail.modifyBtnShow
    },
  },
  methods: {
    setOrderModify() {
      this.$parent.setOrderModify();
      this.$emit('close')
    },
    setOrderOk() {
      this.$emit('close')
    },
    setOrderDelete() {
      if (this.setOrderDetail.orderDetail.isDeleted == "Y") {
        this.$alert("It has already been deleted.", "", "info").then(() => {});
        return false;
      }
      this.$confirm("Order Delete?", "", "question").then(() => {
        var loginId = this.$session.get("loginId");
        var orderId = this.orderDetail.id;
        var authorization = this.$session.get("authorization");
        var vm = this;

        var axios = require("axios");
        var data = JSON.stringify([
          {
            deletedBy: loginId,
            id: orderId,
          },
        ]);

        var config = {
          method: "delete",
          url: common.makeAPIUrl("/sellers/" + loginId + "/orders"),
          headers: {
            Authorization: authorization,
            "Content-Type": "application/json",
          },
          data: data,
        };

        axios(config)
          .then(function (response) {
            if (response.status == 200) {
              vm.$alert("Delete Complate.", "", "info").then(() => {
                vm.$parent.getDataInit();
                vm.$emit('close')
              });
            }
          })
          .catch(function (err) {
            vm.$alert(err.response.data.message, "", "info").then(() => {});
          });
      });
    },    
  }
}
</script>